<template>
  <table>
    <tr class="row-head">
      <td class="col-head"></td>
      <td>当前值</td>
      <td>操作</td>
    </tr>
    <tr>
      <td class="col-head">连接状态：</td>
      <td>{{deviceData.online?"在线":"离线"}}</td>
      <td></td>
    </tr>
    <tr>
      <td class="col-head">当前温度：</td>
      <td>{{deviceData.temperature.toFixed(2)}}</td>
      <td></td>
    </tr>
    <tr>
      <td class="col-head">烟感告警：</td>
      <td>{{deviceData.smokeAlarm?"有告警":"正常"}}</td>
      <td></td>
    </tr>
    <tr>
      <td class="col-head">控制开关：</td>
      <td>{{deviceData.switch?"开":"关"}}</td>
      <td>
        <el-switch
          v-model="switchValue"
          active-color="#13ce66"
          inactive-color="#ff4949"
          @change="setSwitch"
        ></el-switch>
      </td>
    </tr>
  </table>
</template>

<script>
export default {
  name: "FireAlarmConsole",
  data() {
    return {
      switchValue: false
    };
  },
  props: {
    deviceData: Object
  },
  methods: {
    setSwitch(value) {
      this.$emit("set-switch", value);
    }
  }
};
</script>

