<template>
  <el-container>
    <el-header>
      <el-row>
        <el-col :span="12" class="col-info">
          <div>productKey: {{productKey}} / deviceName: {{deviceName}}</div>
        </el-col>
        <el-col :span="12">
          <el-checkbox v-model="autoRefresh">自动刷新</el-checkbox>
          <span>&nbsp;&nbsp;</span>
          <el-button
            type="primary"
            icon="el-icon-refresh"
            size="mini"
            round
            v-on:click="onRefresh"
          >刷新</el-button>
        </el-col>
      </el-row>
    </el-header>

    <el-main>
      <component :is="componentType" :deviceData="deviceData" v-on:set-switch="onSetSwitch"></component>
    </el-main>
  </el-container>
</template>

<script>
import FireAlarmConsole from "@/components/FireAlarmConsole.vue";
import RGBlightConsole from "@/components/RGBlightConsole.vue";
import Iot from "../lib/iot/iot.js";
// import Fc from "../lib/fc";

var iot = new Iot();

export default {
  name: "Console",
  data() {
    return {
      deviceName: "",
      productKey: "",
      deviceData: {
        online: false,
        temperature: 0,
        smokeAlarm: false,
        switch: false
      },

      autoRefresh: true
    };
  },
  computed: {
    componentType: function() {
      switch (this.productKey) {
        case "a1zTwoqIdfD":
        case "a1hhfKQy7R8":
          return FireAlarmConsole;
          break;
        case "a10DlRLjXYj":
          return RGBlightConsole;
          break;
        default:
      }
    }
  },

  beforeMount() {
    this.deviceName = this.$route.params.dn;
    this.productKey = this.$route.params.pk;

    this.timer = setInterval(() => {
      if (this.autoRefresh) this.refresh(this.productKey, this.deviceName);
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    onSetSwitch(value) {
      iot
        .setSwitch(this.productKey, this.deviceName, value ? 1 : 0)
        .then(response => {
          if (response == undefined) return;

          var data = response.data;
          if (!data.Success)
            console.error("SetSwitch fail: ", data.ErrorMessage);
        })
        .catch(err => {
          console.error("onSetSwitch error: %s", err);
        });
    },
    refresh(pk, dn) {
      iot.getDeviceStatus(pk, dn).then(status => {
        this.deviceData.online = status === "ONLINE";
      });

      iot.getFireAlarmDeviceData(pk, dn).then(data => {
        this.deviceData.temperature = data.temperature;
        this.deviceData.smokeAlarm = data.smokeAlarm;
        this.deviceData.switch = data.switch;
      });
    },
    onRefresh() {
      this.refresh(this.productKey, this.deviceName);
    }
  },
  watch: {
    $route(to, from) {
      this.deviceName = to.params.dn;
      this.productKey = to.params.pk;
    }
  }
};
</script>

